body{
    
}
.form>div{
    margin:10px 0px;
}
.form{
}
p{
    padding: 0px;
    margin: 0px;
}

.task{
    margin: 50px 0px;
    border-radius: 5px;
    width: 380px;
}

.task-form input{
    outline: none;
}

.form-container{
    position: relative;
}

.task-form{
    overflow: hidden;
    margin-top: 0px;
    padding: 10px;
    box-sizing: border-box;
    border :1px solid #eaeaea;
    background-color: rgb(237, 250, 254);;
    align-items: center;
    border-radius: 0px 0px 3px 3px;
    transition-property: all;
    transition-duration: 2s;
    transition-timing-function: ease-in-out;
}


.task-bar{
    border: 1px solid hsl(0, 0%, 92%);
    border-bottom: 0;
    padding: 10px;
    box-sizing: border-box;
    background-color: #f2f2f2;
    border-radius: 3px 3px 0px 0px;
    display: flex;
    justify-content: center;
    align-items: center;
    justify-content: space-between;
    font-weight: bold;
   
}

.task-bar i{
    cursor: pointer;
    /* border-left: 2px solid black; */

}

.task-bar>p>span{
    color: #5f5f5f;
}
hr{
    margin: 0;
}
#TaskDesc{
    outline: none;
    padding: 6px;
    width: 100%;
    border: 1px solid #ded9d9;
    border-radius: 4px;
}

.select-op{
    border: none;
    outline: none;
}

.cancel-save-btn{
    margin-top:20px 0px;
    display: flex;
    justify-content: space-between;
}
.btn{
    border: 1px solid ;
    color: white;
    width: 80px;
}
.btn:hover{
    color: white;
}

.delete-btn{
    background-color: rgb(187, 0, 0);
}

.save-btn{
    background-color: rgb(4, 73, 4);
}


.container{
    display: flex;
    flex-direction: column;
}

.Date_Time > div {
    font-size: 1.4rem;
    margin-bottom: 5px;
  }

  .Date_Time {
    display: flex;
    justify-content: space-between;
    margin-bottom: 5px;
  }

  .Date_set {
    width: 120px;
    font-size: 14px;
    padding: 8px;
    z-index: 500;
    border: 1px solid #dbdbdb;
    background-color: white;
    border-radius: 4px;
  }

  .date {
    position: relative;
    margin: 5px 15px 5px 0;
  }

  .date_icon {
    position: absolute;
    right: 0;
    margin: 7px;
    cursor: pointer;
    z-index: 1000;
  }

  .Time_set {
    width: 120px;
    font-size: 14px;
    padding: 8px;
    z-index: 500;
    border: 1px solid #dbdbdb;
    background-color: white;
    border-radius: 4px;
  }

  .time {
    position: relative;
    margin: 5px 0px 5px 0;
  }
  .time_icon {
    position: absolute;
    right: 0;
    margin: 7px;
    cursor: pointer;
    z-index: 1000;
  }

  .m-font{
      font-size:17px;
  }